<template>
    <display>
        <template slot="main">
            <el-form  label-position='right' label-width="80px" :model="form_data" status-icon :rules="rules" ref="form_data">
                <div>
                    <el-row>
                        <el-col :span="12"><div>
                             <el-form-item required label="姓名">
                                <el-input disabled  v-model="form_data.name"></el-input>
                            </el-form-item>
                        </div></el-col>
                         <el-col :span="12"><div>
                            <el-form-item label="奖品编号" prop="prize_id">
                                <el-input :disabled="disabled" v-model="form_data.prize_id" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                         <!-- <el-col :span="12"><div>
                            <el-form-item label="头像">
                                <img style="width:100px" :src="form_data.portrait" alt="">
                            </el-form-item>
                        </div></el-col> -->
                    </el-row>
                    <el-row>
                        <!-- <el-col :span="12"><div>
                            <el-form-item label="奖品编号" prop="prize_id">
                                <el-input :disabled="disabled" v-model="form_data.prize_id" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col> -->
                         <el-col :span="12"><div>
                            <el-form-item >
                                <div style="text-align:center;font-size:30px;width:300px">{{form_data.name}}</div>
                                <img style="width:300px" :src="form_data.portrait" alt="">
                            </el-form-item>
                        </div></el-col>
                        <el-col :span="12" style="padding:0 0 0 150px">
                            <div style="width:300px;height:350px;border:1px solid grey">
                            <div style="width:300px;height:40px;font-size:30px;text-align:center"><span>{{form_data.prize_name}}</span></div>
                            <img style="width:100%;height:300px" :src="form_data.imgUrl" alt="">
                        </div></el-col>
                    </el-row>
                </div>
                    <div class="form_button">
                        <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form_data')">确定</el-button></span>
                         <span style="padding:0 0 0 100px"><el-button @click="goBack()">下一个</el-button></span>
                    </div>
            </el-form>
        </template>
    </display>
</template>

<script>
    import App from '../App'
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    export default {
        inject:['reload'],
        name:'prize_edit',
        components:{
            "display":display
        },
        data(){
            var check_value = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error('奖品ID不能为空'))
                }
            }
            return {
                form_data:{
                    name:'',
                    portrait:'',
                    open_id:'',
                    prize_id:'',
                    imgUrl:'',
                    prize_name:''
                },
                disabled:'',
                rules:{
                    prize_id:[
                        {validator:check_value,trigger:'blur'}
                    ]
                }
            }
        },
        methods :{
           submitForm(form_data){
               var _this = this
               var data = _this.form_data
              // var open_id = _this.form_data.open_id
               axios.post(baseUrl.basePath+'/oper/draw_employee_prize',{
                    prize_id:data.prize_id,
                    open_id :data.open_id
               }).then(function(res){
                if(res.status == '200'){
                     _this.$message({
                        showClose:true,
                        message:'操作成功',
                        type:'success',
                    })
                    var refresh = _this.$router.history.current.fullPath
                    _this.reload() 
                }else{
                    _this.$message({
                        showClose:true,
                        message:'操作失败',
                        type:'error'
                    })
                }
               }).then(function(err){
               })
           },
            goBack(){
                this.$router.replace({
                    path:'/draw_list/employee'
                })
            }
        },
        mounted() {
            var _this = this
            var id = _this.$router.history.current.query.id
            axios.get(baseUrl.basePath+"/oper/draw_employee_get",{
                params:{
                   id:id
                }
            }).then(function(res){
                _this.form_data = res.data.result[0]
                var result1 = res.data.result1
                if(result1.length != 0){
                    console.log(res.data.result1)
                    _this.form_data.imgUrl = res.data.result1[0].imgUrl
                    _this.form_data.prize_name = res.data.result1[0].prize_name
                    _this.disabled = true
                }else{
                    _this.disabled = false
                }
            }).then(function(err){

            })
        },
    }
</script>
    
<style scoped>
    /* .el-form-item{
       padding: 0 50px
    } */
    /* .el-row{
        padding: 30px 0 0 0
    }
    .el-col{
        padding: 0 0 0 50px
    } */
    .form_button{
        text-align: center;
        padding: 50px 0 0 0;
    }
</style>



