<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
                 <el-table
                :data="tableData"
                height="500"
                style="width: 100%">
                    <el-table-column fixed prop="id" label="编号" width="150"></el-table-column>
                    <el-table-column prop="prize_name" label="名称" width="220"></el-table-column>
                    <el-table-column prop="price" label="价格" width="220"></el-table-column>
                    <el-table-column prop="imgUrl" label="图片" width="220">
                        <template slot-scope="scope">
                            <img :src="scope.row.imgUrl" style="width:50px;height:50px">
                        </template>
                    </el-table-column>
                    <el-table-column prop="prize_info" label="奖品信息" width="220"></el-table-column>
                    <el-table-column prop="" label="状态" width="220">
                        <template slot-scope="scope">
                            <span v-if="!scope.row.signIn_id">未抽走</span>
                            <span v-else>{{scope.row.name}}抽走</span> 
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="100">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                            <!-- <el-button type="text" size="small">编辑</el-button> -->
                        </template>
                    </el-table-column>
                </el-table>
                 <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
            </template>
        </display>
    </div>
</template>
<script>
    import display from './common/display'
    import axois  from 'axios'
    import baseUrl from '../../config/config'
    export default {
        name:'draw_view_family',
        components:{
            "display":display
        },
        data(){
            return{
                value:[],
                tableData:[],
                total:new Number,
                pageSize:10,
                currentPage: 1,
            }
        },
        methods:{
            handleClick(row){
                this.$router.replace({
                    path:'/draw_list/edit_f?id='+row.id
                })
            },
            handleSizeChange(vals){
                var _this = this
                _this.pageSize = vals
                axios.get(baseUrl.basePath+'/oper/draw_family_listss',{
                    params :{
                        pageNum:1,
                        pageSize:`${vals}`,
                    }
                }).then(function(res){
                    _this.tableData = res.data,
                    _this.total = res.data.length
                }).catch(function(error){

                })
            },
            handleCurrentChange(val){
                var _this = this
                axios.get(baseUrl.basePath+'/oper/draw_family_list',{
                    params :{
                        pageNum:`${val}`,
                        pageSize:_this.pageSize,
                    }
                }).then(function(res){
                    _this.tableData = res.data,
                    _this.total = res.data.length
                }).catch(function(error){
                })
            },
        },
        mounted(){
            var _this = this
            var boolean = 2
            axois.get(baseUrl.basePath + '/oper/draw_family_list',{
                params:{
                    pageNum:1,
                    pageSize:_this.pageSize,
                }
            })
            .then((res) =>{
                _this.tableData = res.data,
                _this.total = res.data.length
            })
        }
    }
</script>
<style scoped>

</style>




