<template>
    <el-container>
        <el-header>
              <div><img style="width:200px;padding:10px 0 0 0;float:left" src="../assets/new_logo.png" alt=""></div>
        </el-header>
        <el-main>
            <el-row type="flex" class="row-bg" justify="space-between">
                <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
                <el-col :span="6"><div class="grid-content bg-purple-light">
                    <el-form :model="login_form" status-icon ref="login_form" label-width="100px" >
                        <el-form-item label="用户名" prop="user_name" :rules="[{ required: true, message: '用户名不能为空'}]">
                            <el-input type="user_name" v-model="login_form.user_name" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="password" :rules="[{ required: true, message: '密码不能为空'}]">
                            <el-input type="password" v-model="login_form.password" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" style="width:100%" @click="submitForm('login_form')">提交</el-button>
                        </el-form-item>
                    </el-form>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
    import axios from 'axios';
    import baseUrl from '../../config/config'
    import { valid } from 'semver';
    export default {
        name:'login',
        data(){
            return {
                login_form:{
                    user_name:'',
                    password:''
                }
            }
        },
        methods:{
            submitForm(formName){
                var _this = this
                _this.$refs[formName].validate((valid) =>{
                    if(valid){
                        axios.post(baseUrl.basePath+'/oper/login',{
                            user_name:_this.login_form.user_name,
                            password:_this.login_form.password
                        }).then((res) =>{
                            if(res.data.data == true){
                                localStorage.setItem('username',_this.login_form.user_name)
                                _this.$message({
                                    showClose:true,
                                    message:'登陆成功！',
                                    type:'success'
                                })
                                setTimeout(() =>{
                                    _this.$router.replace({
                                        path:'/prize_list'
                                    })
                                })
                            }else{
                                _this.$message({
                                    showClose:true,
                                    message:'账号或密码错误！',
                                    type:'error'
                                })
                            }
                        }).then((err) =>{
                            
                        })
                    }else{
                        return false
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .el-row{
        margin: 200px 0 0 0;
    }
</style>



