<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
               <div>
                    <el-table
                    :data="tableData"
                    height="400"
                    style="width: 100%">
                    <el-table-column fixed prop="id" label="编号" width="240"></el-table-column>
                    <el-table-column prop="user_name" label="姓名" width="240"></el-table-column>
                    <el-table-column prop="phone" label="电话" width="240"></el-table-column>
                    <el-table-column prop="family_name" label="公司员工姓名" width="240"></el-table-column>
                    <el-table-column prop="portrait" label="头像" width="240">
                        <template slot-scope="scope">
                            <img :src="scope.row.portrait" style="width:50px">
                        </template>
                    </el-table-column>
                    <el-table-column prop="status" label="审核状态" width="180">
                        <template slot-scope="scope" width="100">
                            <el-select v-model="scope.row.status" placeholder="请选择审核状态" >
                                <el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="100">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">提交</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                 <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
               </div>
            </template>
        </display>
    </div>
</template>
<script>
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    import Vue from 'Vue'
    export default {
        name :'Home',
        components : {
            "display":display
        },
        data(){
            return {
                tableData:[],
                total:new Number,
                pageSize:10,
                currentPage: 1,
                status:[
                    {
                        value : 0,
                        label : '审核未通过'
                    },
                    {
                        value : 1,
                        label : '审核通过'
                    },
                    {
                        value : 2,
                        label : '审核中'
                    },
                    // {
                    //     value : 3,
                    //     label : '等待审核'
                    // }
                ]
                //pageNum:1    
            }
        },
        methods :{
            handleClick(row) {
                var _this = this
                var openid = row.open_id
                axios.put(baseUrl.basePath+'/oper/audit_update',{
                    openid:openid,
                    status:row.status
                }).then((res) =>{
                    if(res.data.code == "200"){
                        _this.$message({
                            showClose:true,
                            message:'操作成功！',
                            type:'success'
                        })
                    }else{
                        _this.$message({
                            showClose:true,
                            message:'操作失败！',
                            type:'error'
                        })
                    }
                }).catch((err) =>{
                })
            },
             handleSizeChange(vals) {
 
                var _this = this
                _this.pageSize = vals
                axios.get(baseUrl.basePath+'/oper/audit_list',{
                    params :{
                        pageNum:1,
                        pageSize:vals,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result
                }).catch(function(error){
                })
            },
            handleCurrentChange(val) {
                var _this = this
                axios.get(baseUrl.basePath+'/oper/audit_list',{
                    params :{
                        pageNum:`${val}`,
                        pageSize:_this.pageSize,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result
                }).catch(function(error){
                })
            },
        },
        mounted() {
            var _this = this
            axios.get(baseUrl.basePath+'/oper/audit_list',{
                params :{
                    pageNum:1,
                    pageSize:_this.pageSize,
                }
            }).then(function(res){
                _this.tableData = res.data.result1,
                _this.total = res.data.result
            }).catch(function(error){
            })
        },     
    }

</script>
<style scoped>
    .block{
        padding: 10px 0 0 0;
        float: left;
    }  
</style>




