<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
                <div style="background:white;height:450px">
                    <el-form label-position='right' label-width="80px" :model="form_data" class="demo-ruleForm" status-icon :rules="rules" ref="form_data">
                        <div>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="名称" prop="name" >
                                        <el-input v-model="form_data.name" autocomplete="off" disabled></el-input>
                                    </el-form-item>
                                </div></el-col>
                                <el-col :span="12"><div>
                                    <el-form-item label="开始时间" prop="start_time">
                                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form_data.start_time" style="width:100%"></el-date-picker>
                                    </el-form-item>
                                </div></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="地点" prop="place">
                                        <el-input @input="location()" v-model="form_data.place" autocomplete="off"></el-input>
                                    </el-form-item>
                                </div></el-col>
                                <el-col :span="12"><div>
                                    <el-form-item label="会议阶段" prop="meeting_status" >
                                        <el-select v-model="form_data.meeting_status" placeholder="请选择类别" >
                                            <el-option label="年会前" value="1"></el-option>
                                            <el-option label="年会中" value="2"></el-option>
                                            <el-option label="年会后" value="3"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="12"><div>
                                    <el-form-item label="会议提醒" prop="note" >
                                        <el-input type="textarea" v-model="form_data.note" autocomplete="off"></el-input>
                                    </el-form-item>
                                </div></el-col>
                            </el-row>
                        </div>
                        <div class="form_button">
                                <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form_data')">提交</el-button></span>
                                <span style="padding:0 0 0 80px"><el-button  @click="cancel()">取消</el-button></span>
                        </div>
                    </el-form>
                </div>
                <!-- <div>
                    <iframe src="https://map.qq.com/api/js?v=2.exp&key=LTTBZ-BCIKJ-MSTFW-KCWJR-GOXVQ-UBBGN" frameborder="0"></iframe>
                </div> -->
                <div>
                    <div id="container" ></div>
                </div>
            </template>
        </display>
    </div>
</template>
<script>
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    import Vue from 'Vue'
    var geocoder
    export default {
        name :'meeting',
        components : {
            "display":display
        },
        data(){
            var check_start_time = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error('请确认年会开始时间'))
                }
            }
            var check_place = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error('年会举行地点不能为空'))
                }
            }
            var check_note = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error('会议提醒不能为空'))
                }
            }
            return {
                form_data:{
                    name:'',
                    start_time:'',
                    place:'',
                    meeting_status:'',
                    note:'',
                },
                LatLng:{},
                    rules:{
                    start_time:[
                        {validator:check_start_time,trigger:'change'}
                    ],
                    place:[
                        {validator:check_place,trigger:'blur'}
                    ],
                    note:[
                        {validator:check_note,trigger:'blur'}
                    ],
                }
            }
        },
        
        methods :{
            submitForm(formData){
                var _this = this
                // _this.init()
                var address = _this.form_data.place
                var geocoder = new qq.maps.Geocoder({
                    complete:function(res){
                        _this.LatLng = res.detail.location;
                        var data = _this.form_data
                        axios.put(baseUrl.basePath+'/oper/meeting',{
                            start_time:data.start_time,
                            place:data.place,
                            note:data.note,
                            meeting_status:data.meeting_status,
                            LatLng:_this.LatLng
                        }).then((res)=>{
                            if(res.data.msg == '更新成功'){
                                _this.$message({
                                    showClose:true,
                                    message:'提交成功',
                                    type:'success',
                                })
                            }else{
                                _this.$message({
                                    showClose:true,
                                    message:'提交失败',
                                    type:'error',
                                })
                            }
                        }).then((err) =>{
                            
                        })
                    }
                })
                 geocoder.getLocation(address)
            },
            init(){
                var myLatlng = new qq.maps.LatLng(30.321449,120.178513)
                var myOPtions = {
                    zoom:12,
                    center:myLatlng,
                    mapTypeId:qq.maps.MapTypeId.ROADMAP
                }
                var map = new qq.maps.Map(document.getElementById('container'),myOPtions)
                geocoder = new qq.maps.Geocoder({
                    complete : function(result){
                       LatLng = result.detail.location
                        map.setCenter(result.detail.location);
                        var marker = new qq.maps.Marker({
                            map:map,
                            position: result.detail.location
                        });
                    }
                });
            },
            // location(){
            //     var _this = this
            //     var address = _this.form_data.place
            //     var geocoder = new qq.maps.Geocoder({
            //         complete:function(res){
            //             _this.LatLng = res.detail.location;
            //         }
            //     })
            // }
        },
        mounted() {
            var that = this
            // that.init()
            axios.get(baseUrl.basePath+'/oper/meeting')
            .then((res) =>{
                that.form_data = res.data[0]
            })
            .then((err) =>{

            })
        },
    }

</script>
<style scoped>
    .el-form-item{
       padding: 0 50px
    }
    .el-input{
        width: 100%
    }
    .el-row{
        padding: 30px 0 0 0
    }
    .el-col{
        padding: 0 0 0 50px
    }
    .form_button{
        text-align: center
    }
</style>




