<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
                <el-table
                :data="tableData"
                height="500"
                style="width: 100%">
                    <!-- <el-table-column fixed prop="id" label="编号" width="150"></el-table-column> -->
                    <el-table-column prop="name" label="姓名" width="220"></el-table-column>
                    <el-table-column prop="phone" label="联系方式" width="220"></el-table-column>
                    <el-table-column prop="portrait" label="头像" width="220">
                        <template slot-scope="scope">
                            <img :src="scope.row.portrait" style="width:50px">
                        </template>
                    </el-table-column>
                    <el-table-column prop="prize_id" label="中奖记录" width="220">
                        <template slot-scope="scope">
                            <span v-if="scope.row.prize_id == null">未中奖</span>
                            <span v-else>{{scope.row.prize_name}}</span> 
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="100">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                            <!-- <el-button type="text" size="small">编辑</el-button> -->
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
            </template>
        </display>
    </div>
</template>

<script>
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    export default {
        name:'draw_view_employee',
        components:{
            "display":display
        },
        data(){
            return{
                input:'',
                item:[],
                tableData:[],
                result1:'',
                total:new Number,
                pageSize:10,
                currentPage: 1,
            }
        },
        methods:{
            handleClick(row){
                var id = Array(row.phone)[0].slice(7,11)
                // var id = row.id
                this.$router.replace({
                    path:'/draw_list/edit_e?id='+id
                })
            },
            handleSizeChange(vals){
                var _this = this
                _this.pageSize = vals
                axios.get(baseUrl.basePath+'/oper/draw_employee_list',{
                    params :{
                        pageNum:1,
                        pageSize:`${vals}`,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result.length
                }).catch(function(error){
                })
            },
            handleCurrentChange(val){
                var _this = this
                axios.get(baseUrl.basePath+'/oper/draw_employee_list',{
                    params :{
                        pageNum:`${val}`,
                        pageSize:_this.pageSize,
                    }
                }).then(function(res){
                    _this.tableData = res.data.result1,
                    _this.total = res.data.result.length
                }).catch(function(error){

                })
            },
        },
        mounted(){
            var _this = this
            axios.get(baseUrl.basePath+'/oper/draw_employee_list',{
                params:{
                    pageNum:1,
                    pageSize:_this.pageSize,
                }
            }).then((res) =>{
                _this.tableData = res.data.result
                _this.result1 = res.data.result1
                _this.total = res.data.result.length
            }).catch((err) =>{

            })
        }
    }
</script>
<style scoped>

</style>




