<template>
    <display>
        <template slot="main">
            <el-form label-position='right' label-width="80px" :model="form_data" class="demo-ruleForm" status-icon :rules="rules" ref="form_data">
                <div>
                    <el-row>
                        <el-col :span="12"><div>
                            <el-form-item label="名称" prop="prize_name">
                                <el-input v-model="form_data.prize_name" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                        <el-col :span="12"><div>
                            <el-form-item label="价格" prop="price">
                                <el-input v-model="form_data.price" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12"><div>
                            <el-form-item label="图片" prop="imgUrl">
                                <input type="file" class="file" name="file" @change="upload" autocomplete="off">
                            </el-form-item>
                        </div></el-col>
                        <el-col :span="12"><div>
                            <el-form-item label="奖品信息" prop="prize_info" >
                                <el-input v-model="form_data.prize_info" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12"><div>
                            <el-form-item label="数量" prop="number" >
                                <el-input v-model="form_data.number" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                    </el-row>
                </div>
                <div class="form_button">
                        <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form_data')">提交</el-button></span>
                        <span style="padding:0 0 0 80px"><el-button  @click="cancel()">取消</el-button></span>
                </div>
            </el-form>
        </template>
    </display>
</template>

<script>
import display from './common/display'
import axios from 'axios'
import baseUrl from '../../config/config'
import { valid } from 'semver';
export default {
    name:'prize_add',
    components:{
        "display":display,
    },
    data(){
        var check_prize_name = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('奖品名称不能为空'))
            }
        }
        var check_number = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('数量不能为空'))
            }
        }
        var check_price = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('价格不能为空'))
            }
        }
        var check_prize_info = (rule,value,callback) =>{
            if(!value){
                return callback(new Error('奖品信息不能为空'))
            }
        }
        return {
            form_data:{
                prize_name:'',
                price:'',
                imgUrl:'',
                prize_info:'',
                number:'',
            },
            rules:{
                prize_name:[
                    {validator:check_prize_name,trigger:'blur'}
                ],
                price:[
                    {validator:check_price,trigger:'blur'}
                ],
                number:[
                    {validator:check_number,trigger:'blur'}
                ],
                prize_info:[
                    {validator:check_prize_info,trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        upload(e){
            var _this = this
            let file = e.target.files[0];      
            let param = new FormData();
            param.append('file',file,file.name);
            param.append('chunk','0');
           // console.log(param.get('file')); 
            let config = {
                headers:{'Content-Type':'multipart/form-data'}
            };  //添加请求头
            axios.post(baseUrl.basePath+'/home/upload',param,config)
            .then(response=>{
                _this.form_data.imgUrl = response.data
            }).catch(function(err){
            })      
        },
        submitForm(formName){
            var _this = this
            axios.post(baseUrl.basePath+'/home/prize_add',{
                data:this.form_data
            }).then(res =>{
                if(res.status == '200'){
                    _this.$message({
                        showClose:true,
                        message:'添加成功！',
                        type:'success'
                    })
                    setTimeout(() =>{
                        _this.$router.go(-1)
                    },2000)
                }else{
                    _this.$message({
                        showClose:true,
                        message:'添加失败',
                        type:'error'
                    })
                }
            }).catch(err => {
                
            })
            
        },
        cancel(){
            this.$router.go(-1)
        }
    }
}
</script>

<style scoped>
    .el-form-item{
       padding: 0 50px
    }
    .el-input{
        width: 100%
    }
    .el-row{
        padding: 30px 0 0 0
    }
    .el-col{
        padding: 0 0 0 50px
    }
    .form_button{
        text-align: center
    }
</style>



