<template>
    <display>
        <template slot="main">
            <el-form  label-position='right' label-width="80px" :model="form_data" status-icon :rules="rules" ref="form_data">
                <div>
                    <el-row>
                        <el-col :span="12"><div>
                             <el-form-item required label="名称">
                                <el-input disabled  v-model="form_data.prize_name"></el-input>
                            </el-form-item>
                        </div></el-col>
                        <el-col :span="12"><div>
                            <el-form-item label="抽奖编号" prop="signIn_id">
                                <el-input :disabled="disabled" v-model="form_data.signIn_id" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col>
                         <!-- <el-col :span="12"><div>
                            <el-form-item label="图片">
                                <img style="width:200px" :src="form_data.imgUrl" alt="">
                            </el-form-item>
                        </div></el-col> -->
                    </el-row>
                    <el-row>
                        <!-- <el-col :span="12"><div>
                            <el-form-item label="抽奖编号" prop="signIn_id">
                                <el-input :disabled="disabled" v-model="form_data.signIn_id" autocomplete="off"></el-input>
                            </el-form-item>
                        </div></el-col> -->
                         <el-col :span="12"><div>
                            <el-form-item>
                                <div style="text-align:center;font-size:30px;width:300px">{{form_data.prize_name}}</div>
                                <img style="width:300px;height:300px" :src="form_data.imgUrl" alt="">
                            </el-form-item>
                        </div></el-col>
                        <el-col :span="12" style="padding:0 0 0 150px">
                            <div style="width:300px;height:341px;border:1px solid grey">
                            <div style="width:300px;height:40px;font-size:30px;text-align:center"><span>{{this.form_data.name}}</span></div>
                            <img style="width:100%;height:300px" :src="this.form_data.portrait" alt="">
                        </div></el-col>
                    </el-row>
                </div>
                    <div class="form_button">
                        <span style="padding:0 80px"><el-button type="primary" @click="submitForm('form_data')">确定</el-button></span>
                         <span style="padding:0 0 0 100px"><el-button @click="goBack()">下一个</el-button></span>
                    </div>
            </el-form>
        </template>
    </display>
</template>

<script>
    import App from '../App'
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    export default {
        inject:['reload'],
        name:'prize_edit',
        components:{
            "display":display
        },
        data(){
            var check_value = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error('抽奖ID不能为空'))
                }
            }
            return {
                form_data:{},
                disabled:'',
                value:[],
                rules:{
                    signIn_id:[
                        {validator:check_value,trigger:'blur'}
                    ]
                }
            }
        },
        methods :{
           submitForm(form_data){
               var _this = this
               var data = _this.form_data
               console.log(data)
              // var open_id = _this.form_data.open_id
               axios.post(baseUrl.basePath+'/oper/draw_update_f',{
                    prize_id:data.id,
                    signIn_id:data.signIn_id
               }).then(function(res){
                if(res.data.code == '200'){
                     _this.$message({
                        showClose:true,
                        message:'操作成功',
                        type:'success',
                    })
                    _this.reload() 
                }else{
                    _this.$message({
                        showClose:true,
                        message:'操作失败,暂无此抽奖号！',
                        type:'error'
                    })
                }
               }).then(function(err){
               })
           },
            goBack(){
                this.$router.replace({
                    path:'/draw_list/family'
                })
            }
        },
        mounted() {
            var _this = this
            var id = _this.$router.history.current.query.id
            axios.get(baseUrl.basePath+'/oper/draw_prize',{
                params:{
                    prize_id:id,
                }
            }).then((res) =>{
                if(res.data.value == 1){
                    _this.form_data = res.data.result[0]
                }else{
                    _this.form_data = res.data.result[0]
                    _this.disabled = false
                }
            }).catch((err) => {
                
            })

        },
    }
</script>
<style scoped>
    /* .el-form-item{
       padding: 0 50px
    } */
    /* .el-row{
        padding: 30px 0 0 0
    }
    .el-col{
        padding: 0 0 0 50px
    } */
    .form_button{
        text-align: center;
        padding: 50px
    }
</style>



