<template>
    <div>
        <display style="background-color: #E9EEF3;">
            <template slot="main" >
                <div class='news'>
                    <div class="content">
                        <el-scrollbar ref="myScrollbar" style="height:100%">
                            <div id = 'news_item' style="width:60%;padding:20px 20px"  v-for="(item,key) in news_history" :key="key">
                                <el-card shadow="hover" >
                                    <div class="clearfix">
                                        <span style="font-size:14px">创建日期：{{item.news_time}}</span>
                                    </div>
                                    <div class="text item">
                                        消息内容：{{item.content}}
                                    </div>
                                </el-card>
                            </div>
                        </el-scrollbar>
                    </div>
                    <div class="input">
                        <el-input v-model="news" name="news" placeholder="输入要发送的消息"></el-input>
                        <el-button type="primary" plain @click="submit">发送</el-button>
                    </div>
                </div>
            </template>
        </display>
    </div>
</template>
<script>
    import display from './common/display'
    import baseUrl from '../../config/config'
    import axios from 'axios'
    import Vue from 'Vue'
    export default {
        inject:['reload'],
        name :'Home',
        components : {
            "display":display
        },
        data(){
            return {
                news:'',
                news_history:[]
            }
        },
        methods :{
            submit(){
                var that = this
                var day = new Date().getHours() +':'+ new Date().getMinutes() +':'+ new Date().getSeconds();
                var year = new Date().getFullYear() +'-'+ (new Date().getMonth()+1) +'-'+ new Date().getDate()
                var time = year +' '+ day;
                axios.post(baseUrl.basePath+'/oper/news',{
                   news:that.news,
                   time:time
                }).then((res) =>{
                    if(res.data.code == 200){
                         that.$message({
                            showClose:true,
                            message:'发送成功',
                            type:'success',
                        })
                        that.news_history.push({
                            content:that.news,
                            news_time:time
                        })
                        that.news = ''
                    }else{
                         that.$message({
                            showClose:true,
                            message:'发送失败',
                            type:'error',
                        })
                    }
                }).then((err) =>{
                   
                })
            },
            scrollDown() {
                this.$refs['myScrollbar'].wrap.scrollTop = this.$refs['myScrollbar'].wrap.scrollHeight
            }
        },
        mounted() {
            var that = this
            axios.get(baseUrl.basePath+'/oper/news')
            .then((res) =>{
                that.news_history = res.data
            }).then((err) =>{

            })
        },
        updated(){
            var that = this
            that.scrollDown()
        },
    }

</script>
<style scoped>
    .block{
        padding: 10px 0 0 0;
        float: left;
    }
    .news{
        width: 80%;
        height: 400px;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        flex-direction: column
    }
    .content{
        width: 100%;
        height: 360px;
    }
    .input{
        width: 100%;
        display: flex;
        flex-direction: row;
        align-content: center;
    }
    .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
    padding: 5px 10px;
  }
  .clearfix{
      padding: 5px 10px;
      border-bottom: grey solid 1px 
  }
  .clearfix:after {
    display: table;
    font-size: 14px;
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
  .el-scrollbar__wrap{
      overflow-x: hidden
  }
</style>




